import React from 'react';

const API_KEY = "a9f020ade0a6ba6d2ba84ba91345fb4e73fa64ad1c0b6221233b55c6f12c3b29";
export class Development extends React.Component {
	state = {
		images: [],
		randomImages: []
	}

	getPhotos = async (e) => {
		e.preventDefault();
		const keySearch = this.keySearch.value;
		const api_call = await fetch(`https://api.unsplash.com/search/photos?per_page=2&query=${keySearch}&client_id=${API_KEY}`)
		const data = await api_call.json();
		console.log(data.results)
		setTimeout(() => {
			this.setState({
				images: data.results
			})
		},1000)	
	}

	componentDidMount(){
		setTimeout(() => {
			fetch(`https://api.unsplash.com/photos?per_page=2&client_id=${API_KEY}`)
			.then(response => response.json())
			.then(data => this.setState({
				randomImages: data
			}))
		},2000)
		
	}

	render(){
		let getSearch = this.state.images.map((image)=>{
					return(

						<div key={image.id}>
							<p> {image.description} </p>
						</div>
					)
				})

		let getRandom = this.state.randomImages.map((random)=>{
					return(
						<div key={random.id}>
							<p> {random.id} </p>
						</div>
					)
				})
		return(
			<div>
				{this.state.randomImages.length === 0 ? 'loading' :
					<div>
						<form onSubmit={(e) => (this.getPhotos(e))}>
							<input type='teks' ref={(input) => (this.keySearch) = input}/>
							<button> get </button>
						</form>
						<div>
							{this.state.images.length === 0 ?  getRandom :  getSearch}
							
						</div>
					</div>
				}
				
			</div>
		)
	}
}